import type { ZhiFactory } from '@ali/4ever-cangjie';
import moFactory from '../mo/index';
import createRenderNode from './createRenderNode';
import onAction from './onAction';
import { actions, isActions } from '../common/actions';
import { commands } from '../common/commands';
import * as queries from '../common/queries';

const zhiFactory: ZhiFactory = (configs) => {
  /* istanbul ignore next */
  if (!configs.refblock) {
    return moFactory({});
  }
  return {
    ...moFactory({}),
    commands,
    actions,
    queries,
    isActions,
    onAction: onAction,
    renderNode: createRenderNode(configs.refblock),
  };
};

export default zhiFactory;
